<script>
  import Name from "./components/Name.svelte";

  const cardList = [...Array(10)].map((_, i) => i + 1);
  const cardList2 = [...Array(10)].map((_, i) => i + 11);

  const getRandomInt = (min, max) => {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min)) + min; //The maximum is exclusive and the minimum is inclusive
  };

  const multiplyMake = () => {
    return `${getRandomInt(1, 10)} x ${getRandomInt(1, 10)}`;
  };
</script>

<style>
  .row {
    margin: 40px auto;
  }

  .card-title {
    color: #ff3e00;
  }

  .mulcon {
    font-size: 1.5em;
    text-align: center;
  }
</style>

<div class="container">
  <div class="row">
    <Name />
    <!--  -->
    <div class="col s6 m6 l6">
      {#each cardList as idx}
        <div class="card">
          <div class="card-content">
            <div class="card-title">{idx}</div>
            <div class="mulcon" style="margin-top: -2.5em; margin-bottom: -1em">
              {multiplyMake()} =
              <div class="input-field inline" style="width: 3em">
                <input type="text" value="" />
              </div>
            </div>
          </div>
        </div>
      {/each}
    </div>
    <div class="col s6 m6 l6">
      {#each cardList2 as idx}
        <div class="card">
          <div class="card-content">
            <div class="card-title">{idx}</div>
            <div class="mulcon" style="margin-top: -2.5em; margin-bottom: -1em">
              {multiplyMake()} =
              <div class="input-field inline" style="width: 3em">
                <input type="text" value="" />
              </div>
            </div>
          </div>
        </div>
      {/each}
    </div>

  </div>
</div>
